* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100vh;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.card {
  border: 1px solid #f3f3f3;
  flex-direction: column;
  width: 200px;
  height: 408px;
  padding-top: 32px;
  transition: transform .3s ease-in-out;
  display: flex;
}

.card:hover {
  transform: scale(1.2);
}

.card__container {
  padding: 0 16px 16px;
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__img img {
  width: 100%;
  height: 100%;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
  transition: color .3s ease-in-out;
}

.card:hover .card__title {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  font-size: 10px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.5;
}

.card__amount {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.13;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #0000;
}

.card:hover .card__button {
  color: #00acdc;
  background-color: #fff;
}

.card-text {
  font-size: 10px;
  font-weight: 400;
  line-height: 1.4;
}

.stars {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 0;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin: 0 2px;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__link {
  width: 40px;
  height: 40px;
  display: block;
}

.header__link img {
  width: 100%;
  height: 100%;
}

.header__nav {
  align-items: center;
  display: block;
}

.header__nav-list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header__nav-item {
  margin-left: 20px;
}

.header__nav-item:first-child {
  margin-left: 0;
}

.header__nav-link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .2s;
  display: flex;
  position: relative;
}

.header__nav-link:hover {
  color: #00acdc;
}

.header__nav-link.is-active {
  color: #00acdc;
  position: relative;
}

.header__nav-link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}
